.password-control {
    position: relative;
    width: 100%;
}

.LogIn {
    padding: 100px 0;
    background-size: 65%;
    background-repeat: no-repeat;
    background-position: left;
}

.password-control i {
    position: absolute;
    top: -41px;
    margin-right: 10px;
}

input {
    border: none;
}

